// 使用者點擊分類後之頁面
<template>
  <div>
      <!-- <Loading :active="isLoading"></Loading> -->
      <div class="row mt-4">
          <!-- 產品列表 -->
            <ol class="breadcrumb ms-2">
              <li class="breadcrumb-item" aria-current="page"><router-link  class=" text-decoration-none text-dark bg-light rounded-pill p-2" to="/user/index">Home</router-link></li>
              <li class="breadcrumb-item"><router-link  class=" text-decoration-none text-dark bg-light rounded-pill p-2" :to="`/user/${this.category}`">{{this.category}}</router-link></li>
            </ol>
            <div class="user-product-area d-flex flex-wrap">
              <!-- <template v-for="product in products" :key="product.id">
                v-if -> 只顯示與分類相同之產品
                <div class="user-product" v-if = "product.category === this.category">
                  <ProductCard :product="product" @add-to-cart="addToCart(product.id)"></ProductCard>
                  <hr>
                </div>
              </template> -->
              <template v-for="product in page_product" :key="product.id">
                <div class="user-product">
                  <ProductCard :product="product" @add-to-cart="addToCart(product.id)"></ProductCard>
                  <hr>
                </div>
              </template>
            </div>
            <UserPagination :pages="totalPage" @user-product-page="NowPage"></UserPagination>
      </div>
  </div>
</template>

<script type="module">
import ProductCard from '../components/ProductCard.vue'
import UserPagination from '../components/UserPagination.vue'
export default {
  data () {
    return {
      products: {}, // 存取api回傳的全部產品
      // isLoading: false,
      category: '', //  接收分類
      categorized_product: [], // 存取分類後的產品
      totalPage: 0, // 分頁總頁數
      perpage: 12, // 每頁產品數量
      page_product: [], // 每頁分頁產品
      nowPage: 1
    }
  },
  components: {
    ProductCard,
    UserPagination
  },
  methods: {
    // 加入購物車
    addToCart (id) {
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/cart`
      // 加入讀取效果
      // this.status.loadingItem = id
      const cart = {
        product_id: id,
        // 數量預設為一
        qty: 1
      }
      // this.isLoading = true
      this.$http.post(url, { data: cart })
        .then((res) => {
          this.$pushMessageState(res, '加入購物車')
          this.emitter.emit('cart-length-change')
          // this.isLoading = false
        })
    },
    // 取得全部已啟用的產品
    getProducts () {
      // this.isLoading = true
      // 這是取得分頁產品api
      // const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/products?page=${page}`
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/products/all`
      // axios的$http方法
      this.$http.get(api)
        .then((res) => {
          // this.isLoading = false
          if (res.data.success) {
            this.products = res.data.products
            this.categorized_product = []
            this.products.forEach(element => {
              if (element.category === this.category) {
                this.categorized_product.push(element)
              }
            })
            this.totalPage = Math.ceil(this.categorized_product.length / this.perpage)
            this.nowPage = 1
            this.pagination(this.nowPage)
          }
        })
    },
    // 前往商品詳細頁面
    getProduct (id) {
      this.$router.push(`usersingleproduct/${id}`)
    },
    // 計算總價，眼殘api就會回傳，但沒關係我們自己寫一次
    countTotal () {
      this.total = 0
      // Object.values遍歷物件的value值，再使用foreach加總
      Object.values(this.carts).forEach(item => {
        this.total += item.total
      })
    },
    // 分頁產品
    pagination (Nowpage) {
      console.log(Nowpage)
      if (Nowpage > this.totalPage) {
        Nowpage = this.totalPage
      }
      const max = Nowpage * this.perpage
      const min = (Nowpage - 1) * this.perpage + 1
      this.categorized_product.forEach((item, index) => {
        const num = index + 1
        if (num >= min && num <= max) {
          this.page_product.push(item)
        }
      })
    },
    // 呈現當前頁面資料
    NowPage (nowPage) {
      this.nowPage = nowPage
      this.page_product = []
      this.pagination(this.nowPage)
    }
  },
  created () {
    // 初始化產品列表
    this.category = this.$route.params.category
    this.getProducts()
    // 接收分類並重新取得分類產品
    this.emitter.on('get-product', (category) => {
      this.category = category
      this.page_product = []
      this.getProducts()
    })
  },
  inject: ['emitter']
}
</script>

<style lang="scss">
@import "../assets/mixins.scss";
  .product-img{
    height: 100%;
    width: 100%;
  }
  .bottom-border{
    border-bottom: 1px solid #000;
  }
  .user-product-area{
    @include pad(){
      width: 90%;
      margin: 0 auto;
    }
    .user-product{
      width: 25%;
      @include pad(){
        width: 33%;
      }
      @include smallpad(){
        width: 50%;
      }
      @include phone(){
        width: 100%;
      }
    }
  }
</style>
