// 使用者分頁元件
<template>
  <nav aria-label="Page navigation example">
    <ul class="pagination justify-content-center">
      <li class="page-item" :class="{'disabled' : this.Nowpage === 1}">
        <a class="page-link d-block" href="#" aria-label="Previous" @click.prevent="updatePage" :data-page="this.Nowpage-1">
          <span aria-hidden="true">&laquo;</span>
        </a>
      </li>
      <li class="page-item"  v-for="page in pages" :key="page"  :class="{'active' : page === this.Nowpage}">
        <a class="page-link d-block" href="#" @click.prevent="updatePage" :data-page="page">
            {{page}}
        </a>
      </li>
      <li class="page-item" :class="{'disabled' : this.Nowpage === pages}">
        <a class="page-link d-block" href="#" aria-label="Next" @click.prevent="updatePage" :data-page="this.Nowpage+1">
          <span aria-hidden="true">&raquo;</span>
        </a>
      </li>
    </ul>
  </nav>
</template>

<script>
// :pages="{ 頁碼資訊 }"
// @emitPages="更新頁面事件"
export default {
  data () {
    return {
      Nowpage: 1 // 當前頁數，預設為一
    }
  },
  // 使用Props取得pagination
  props: ['pages'],
  methods: {
    // 使用emit將當前頁傳給Product.vue
    updatePage (e) {
      if (e.target.nodeName !== 'A') return
      this.Nowpage = Number(e.target.dataset.page)
      this.$emit('user-product-page', this.Nowpage)
    }
  }
}
</script>
