// 選擇分類後各分類的商品卡片
<template>
    <div class="card border-0 mt-4 card-width d-flex flex-column align-items-center">
      <img :src="product.imageUrl" class="card-img-top cursor-pointer product-img" alt="..." @click="getProduct(product.id)">
      <button type="button" class="btn btn-cart-green py-2 mt-4 cart-proceed-button fw-bold" @click="$emit('add-to-cart')"><i class="bi bi-cart3 me-2"></i>Add To Cart</button>
      <div class="card-body product-card-body p-0 pt-2">
          <div class="h5 card-title cursor-pointer fw-bold"  @click="getProduct(product.id)">{{product.title}}</div>
          <p class="card-text cart-gray mt-4">${{product.price}}/ {{product.unit}}</p>
      </div>
    </div>
</template>

<script>
export default {
  props: ['product'],
  methods: {
    // 前往商品詳細頁面
    getProduct (id) {
      this.$router.push(`/user/usersingleproduct/${id}`)
    }
  }
}
</script>

<style lang="scss">
@import "../assets/mixins.scss";
.card-width{
  width: 90%;
  img{
    cursor: pointer;
  }
  .cart-proceed-button{
    width: 100%;
    @include pad(){
      font-size: 14px;
    }
  }
  .product-card-body{
    width: 90%;
    .card-title{
        font-style: normal;
        display: flex;
        min-height: 72px;
        cursor: pointer;
        @include pad(){
          font-size: 16px;
          min-height: 50px;
        }
    }
    .card-text{
      @include pad(){
        font-size: 14px;
      }
    }
  }
}
</style>
